*{
  padding:0;margin:0;
  box-sizing:border-box;
}
.contain{
  width: 100vw;
  height:100vh;
  display: grid;
  place-items:center;
  background:crimson;
 overflow:hidden;
}

.containCard{
  width: 50vmin;
  height:70vmin;
  background:rgb(255, 179, 0);
  position:relative;
  display: grid;
  place-items:center;
 box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.089),
 0px 0px 30px transparent,
 0px 0px 40px black;
}
.head{
  position: absolute;
  top: 0%;
  background:white;
  font-size:3vmin;font-weight: bold;
  width: 100%;
  display:grid;
  place-items: center;
  height:8vmin;
}
.list{
  width: 95%;
  height:80%;
  padding: 2vmin;
  display: flex;
  flex-direction: column;
  gap:5px;
  justify-content: space-around;
  overflow: scroll;
  position: absolute;
}
.cards:hover{
  background:black;
}
.cards:hover  .name{
  color:rgb(255, 179, 0);
}
.cards:hover  .age{
  color:rgb(235, 233, 230);
}
.cards{
  width: 100%;
  height:9vmin;;
  position:relative;
  background:white;
  transition: all 200ms linear;
}
.cards > img{
  width: 6vmin;
  height:6vmin;
  border-radius:50%;
  position: absolute;
  left:5%;
  top:13%;
}
.name{
  position: absolute;
  font-weight: bold;
  left:40%;
  top:4%;
  font-size: 3vmin;

}
.age{
  position: absolute;
  left:40%;
  top:50%;
}

#clear{
  width: 90%;
  height:4vmin;
  border:none;
  outline:none;
  background:white;
  color:black;
  font-size: 2vmin;
  text-transform: uppercase;
  position:absolute;
  bottom:2%;;
}

@media only screen and (max-width:550px){
  .containCard{
    width:70vmin;
    height:100vmin;
  }
  .cards{
    height:15vmin;
  }
  .name{
    font-size: 4vmin;
  }
  .age{
    font-size: 3.5vmin;
  }
  .cards > img{
    width: 8vmin;
    height:8vmin;
  }
}